.full2 {
    overflow-x: hidden;
    overflow-y: auto;
    min-height: 100vh;
    
    &__wrapper {
        display: flex;
        min-height: 100vh;
        

        @include vp-767 {
            height: 100%;
        }

        // @media screen and (max-width: 550px) {
        //     min-height: auto;
        // }
    }



    &__left {
        
        width: 40%;
        position: relative;
        display: flex;
        flex-direction: column;
        align-items: center;
        padding: 30px 30px 30px 0;
        min-height: 100vh;
        transition: .3s;
        &::after {
            content: "";
            position: absolute;
            top: 0;
            left: -100vw;
            width: calc(100% + 100vw);
            height: 100%;
            background: rgba(107, 161, 218, .55);
            z-index: -1;

            @media screen and (max-width: 550px) {
                background: rgba(107, 161, 218, 1);
            }
        }

        @media screen and (max-width: 550px) {
            width: 400px;
            height: 100%;
            transform: translateX(-100%);
            position: fixed;
            left: 0;
            padding: 30px;
            overflow: auto;
            z-index: 999;
        }

        @media screen and (max-width: 450px) {
            width: 300px;
        }

    }


    &.open {
        .full2__left {
            transform: translateX(0);
        }
    }

    &__close {
        display: none;
        position: absolute;
        right: -15px;
        top: -5px;
        svg {
            width: 25px;
            height: 25px;
        }
        @media screen and (max-width: 550px) {
            display: block;
        }
    }

    &__left-content {
        display: flex;
        flex-direction: column;
        height: 100%;
        position: relative;
        
        @media screen and (max-width: 550px) {
            width: 100%;
        }
    }

    &__label {
        
        font-weight: 700;
        font-style: Bold;
        font-size: 18px;
        leading-trim: CAP_HEIGHT;
        line-height: 100%;
        letter-spacing: 0px;
        vertical-align: middle;
        color: rgba(255, 255, 255, 1);
        margin: 0 0 20px;
    }

    &__change {
        display: none;

        @media screen and (max-width: 550px) {
            display: flex;
            justify-content: space-between;
            align-items: center;
        }
    }

    &__change-button {
        display: flex;
        flex-direction: column-reverse;
        justify-content: start;
        gap: 7px;

        span {
            background: rgba(0, 0, 0, 1);
            width: 22px;
            height: 3px;
        }

        span:nth-child(1) {
            width: 15px;
            top: 7px;
            height: 3px;
        }
    }

    &__ch-label {
        
        font-weight: 700;
        font-style: Semibold;
        font-size: 17px;
        leading-trim: NONE;
        line-height: 22px;
        letter-spacing: 0px;
        text-align: center;
        vertical-align: middle;
        margin: 0;
    }

    &__links-left {
        display: flex;
        flex-direction: column;
        gap: 10px;
        margin-bottom: 10px;

        
    }

    &__link-left {
        
        font-weight: 400;
        font-style: Regular;
        font-size: 16px;
        leading-trim: NONE;
        line-height: 110.00000000000001%;
        letter-spacing: 0px;
        color: rgba(58, 58, 58, 1);

        &--active {
            font-weight: 700;
        }

        @include vp-389 {
            font-size: 12px;
        }
    }

    &__links-left-bottom {
        display: flex;
        flex-direction: column;
        gap: 10px;
        margin-top: auto;

        .full2__link-left {
            font-weight: 600;
        }
    } 

    

    &__right {
        width: 70%;
        padding: 30px 0 30px 115px;
        display: flex;
        flex-direction: column;

        @media screen and (max-width: 950px) {
            padding: 30px 0 30px 30px;
        }

        @media screen and (max-width: 550px) {
            width: 100%;
            padding: 30px 0 0;
        }
    }

    &__top {
        position: relative; 
        padding-bottom: 20px;
        display: flex;
        

        &::after {
            content: '';
            position: absolute;
            bottom: 0;
            left: -115px;
            width: 100vw;
            height: 1px;
            background: rgba(107, 161, 218, 1);
            transform: translateX(calc(-50vw + 50%)); 

            @media screen and (max-width: 950px) {
                left: -30px;
            }

            
            @include vp-389 {
                left: -20px;
            }
        }

        @media screen and (max-width: 550px) {
            display: none;
        }
    }

    &__icon {
        display: flex;
        align-items: center;
        gap: 5px;
    }

    &__user {
        svg {
            width: 31px;
            height: 32px;
        }

        &--text {
            
            font-weight: 400;
            font-style: Regular;
            font-size: 18px;
            leading-trim: NONE;
            line-height: 110.00000000000001%;
            letter-spacing: 0px;
            color: rgba(58, 58, 58, 1);
            margin-left: 10px;
        }
    }

   

    &__title {
        
        font-weight: 500;
        font-style: Medium;
        font-size: 20px;
        leading-trim: CAP_HEIGHT;
        line-height: 130%;
        letter-spacing: 0px;
        color: rgba(58, 58, 58, 1);
        margin: 30px 0 37px;

        @include vp-767 {
            font-size: 18px;
        }
    }

    &__bottom {
        ul {
            display: flex;
            flex-direction: column;
            gap: 40px;
        }

        li {
            display: grid;
            grid-template-columns: 1fr 1.5fr;
            align-items: center;
            gap: 15px;

            @media screen and (max-width: 820px) {
                grid-template-columns: 1fr 1fr;
                gap: 5px;
            }

        }
    }

    &__name {
        
        font-weight: 500;
        font-style: Medium;
        font-size: 18px;
        leading-trim: CAP_HEIGHT;
        line-height: 130%;
        letter-spacing: 0px;
        margin: 0;
        color: rgba(49, 119, 188, 1);

        @include vp-767 {
            font-size: 16px;
        }
    }

    &__pus {
        
        font-weight: 500;
        font-style: Medium;
        font-size: 18px;
        leading-trim: CAP_HEIGHT;
        line-height: 130%;
        letter-spacing: 0px;
        color: rgba(0, 0, 0, 1);
        margin: 0;

        @include vp-767 {
            font-size: 16px;
        }

        @media screen and (max-width: 330px) {
            font-size: 14px;
        }
    }

    &__pus-link {
        white-space: wrap;
        font-weight: 400;
        font-style: Regular;
        font-size: 18px;
        leading-trim: CAP_HEIGHT;
        line-height: 130%;
        letter-spacing: 0px;
        background: rgba(180, 209, 235, 1);
        padding: 10px;
        border-radius: 10px;
        width: fit-content;
        transition: .3s;

        @include vp-767 {
            font-size: 16px;
            padding: 10px 6px;
        }

        @media screen and (max-width: 330px) {
            font-size: 14px;
        }
    }

    &__pus-link:hover {
        opacity: 0.7;
    }

    &__full3 {
        margin-top: 30px;
    }

    .phone__form {
        margin-bottom: 20px;
    }

    .phone__form form input {
        padding: 10px 14px 10px 45px;
        width: 230px;
    }

    .phone__form form input::placeholder {
        @media screen and (max-width: 600px) {
           font-size: 14px;
        }
    }

    &__list-users {
        display: flex;
        flex-direction: column;
        gap: 20px !important;

        li {
        
            font-weight: 500;
            font-style: Medium;
            font-size: 16px;
            leading-trim: CAP_HEIGHT;
            line-height: 130%;
            letter-spacing: 0px;

        }
    }

    .custom-input {
        max-width: 375px;
        width: 100%;
    }


    .custom-input input {
        border-radius: 30px;
        padding: 20px;
        height: 50px;
        background-color: rgba(180, 209, 235, 1);
        color: rgba(142, 142, 147, 1);
    }

    .custom-input input::placeholder {
        
        font-weight: 500;
        font-style: Medium;
        font-size: 16px;
        leading-trim: CAP_HEIGHT;
        line-height: 130%;
        letter-spacing: 0px;
        color: rgba(142, 142, 147, 1);

        @include vp-389 {
            font-size: 11px;
        }
    }

    &__full4 {
        display: flex;
        flex-direction: column;
        gap: 10px;
    }

    &__full4-item {
        display: flex;
        align-items: center;
        gap: 10px;
    }

    &__full4-item p {
        max-width: 220px;
        width: 100%;
        font-weight: 500;
        font-style: Medium;
        font-size: 16px;
        leading-trim: CAP_HEIGHT;
        line-height: 130%;
        letter-spacing: 0px;
        margin: 0;
        color: rgba(49, 119, 188, 1);

        @media screen and (max-width: 950px) {
            max-width: 120px;
        }

        @media screen and (max-width: 650px) {
            font-size: 14px;
            max-width: 100px;
        }
    }

    .custom-select {
        max-width: 375px;
        width: 100%;
        position: relative;
    }

    .btn-reset.custom-select__button {
        border-radius: 30px;
        padding: 20px;
        height: 50px;
        background-color: rgba(180, 209, 235, 1);
    }

    .custom-select__text {
        
        font-weight: 500;
        font-style: Medium;
        font-size: 16px;
        leading-trim: CAP_HEIGHT;
        line-height: 130%;
        letter-spacing: 0px;
        color: rgba(142, 142, 147, 1);
        text-align: left;
        @include vp-389 {
            font-size: 11px;
        }
    }

    .custom-select__icon {
        width: 14px;
        height: 18px;
        clip-path: none;
        background: none;
        svg {
            width: 14px;
            height: 12px;
        }
    }

    .custom-select__list {
        background: rgba(254, 255, 255, 1);
        border-radius: 20px;
        height: auto;
        padding: 15px 20px;
        display: none;
    }

    .custom-select.is-open .custom-select__list {
        display: block;
    }

    .custom-select__item {
        display: block;
        font-weight: 500;
        font-style: Medium;
        font-size: 16px;
        leading-trim: CAP_HEIGHT;
        line-height: 130%;
        letter-spacing: 0px;
        color: rgba(142, 142, 147, 1);
    }

    &__full4-btn {
        margin-top: 20px;
        display: flex;
        align-items: center;
        max-width: 375px;
        width: 100%;
        border-radius: 60px;
        background: #000;
        padding: 15px;
        color: #fff;
        font-size: 16px;
        leading-trim: NONE;
        line-height: 100%;
        -webkit-box-orient: horizontal;
        -webkit-box-direction: reverse;
        -ms-flex-direction: row-reverse;
        flex-direction: row-reverse;
        -webkit-box-pack: center;
        -ms-flex-pack: center;
        justify-content: center;
        gap: 10px;
        -webkit-transition: .3s;
        -o-transition: .3s;
        transition: .3s;

        &--mobile {
            display: none;

            @include vp-767 {
                display: flex !important;
                max-width: 100%;
            }
        }

        @include vp-767 {
            display: none;
        }
    }

    &__full4-btn:hover {
        opacity: 0.7;
    }
}